<script>
  import { onMount } from "svelte";
  import { text_area_resize } from "./auto_resize_height.js";

  let dataAdded = false;
  let input = "";

  let extraFiltered = [];
  let cleaned = [];
  onMount(async () => {
    // This is cool, but not sure I want to use it, keep it simple
    document.addEventListener("mouseup", event => {
      if (window.getSelection().toString().length) {
        const selected = window.getSelection().toString();
        extraFiltered.push({
          word: selected,
          interest: 1
        });
        extraFiltered = extraFiltered;
      }
    });
  });

  function nextStep() {
    dataAdded = true;
  }

  function reset() {
    dataAdded = false;
    input = "";
    words = words.map(e => {
      e.interest = 0;
      return e;
    });
    extraFiltered = [];
    cleaned = [];
  }

  function toggle(e, index) {
    e.interest = e.interest == 1 ? 0 : 1;
    words[index] = e;
  }

  function remove(e) {
    e.interest = 0;
    words[e.originalIndex] = e;
  }

  $: words = input.split(" ").map((e, index) => {
    return { word: e, interest: 0, originalIndex: index, cleaned: "" };
  });

  $: filtered = words.filter(e => e.interest == 1);

  $: cleaned = filtered.map(e => {
    var punctuationless = e.word.replace(/[.,\/#!$%\^&\*;:{}=\-_`~()]/g, "");
    var finalString = punctuationless.replace(/\s{2,}/g, " ");
    e.cleaned = finalString;
    return e;
  });
</script>

<style>/*! TACHYONS v4.12.0 | http://tachyons.io */
/*! normalize.css v8.0.0 | MIT License | github.com/necolas/normalize.css */
h1{font-size:2em;margin:.67em 0}
button,textarea{font-family:inherit;font-size:100%;line-height:1.15;margin:0}
button{overflow:visible}
button{text-transform:none}
button{-webkit-appearance:button}
button::-moz-focus-inner{border-style:none;padding:0}
button:-moz-focusring{outline:1px dotted ButtonText}
textarea{overflow:auto}
.border-box,div,h1,li,p,textarea,ul{box-sizing:border-box}
.outline{outline:1px solid}
.ba{border-style:solid;border-width:1px}
.b--black-20{border-color:rgba(0,0,0,.2)}
.br2{border-radius:.25rem}
.br3{border-radius:.5rem}
.db{display:block}
.flex{display:flex}
.flex-column{flex-direction:column}
.h1{height:1rem}
.w-50{width:50%}
.w-100{width:100%}
.hover-black:focus,.hover-black:hover{color:#000}
.pa0{padding:0}
.pa2{padding:.5rem}
.pa3{padding:1rem}
.mr2{margin-right:.5rem}
.mb2{margin-bottom:.5rem}</style>

<div class="flex flex-column">
  {#if !dataAdded}
    <div class="outline w-50 pa3 mr2">
      <h1>Add Text</h1>
      <textarea
        class="db border-box hover-black w-100 ba b--black-20 pa2 br2 mb2"
        aria-describedby="comment-desc"
        bind:value={input}
        use:text_area_resize />
      <button class="br3" on:click={nextStep}>Next</button>
    </div>
  {/if}

  {#if dataAdded}
    <div class="outline w-50 pa3 mr2">

      <button class="br3" on:click={reset}>Reset</button>

      <h1>Select Text</h1>
      <p>
        {#each words as w, i}
          <span class="pa0" on:click={() => toggle(w, i)}>
            {#if w.interest == 0}
              {w.word}
            {:else}
              <mark>{w.word}</mark>
            {/if}
          </span>
        {/each}
      </p>
    </div>

    {#if cleaned.length > 0}
      <div class="outline w-50 pa3 mr2">
        <h1>View Words</h1>
        <ul>
          {#each cleaned as w, i}
            <li on:click={() => remove(w)}>
              <span>{w.cleaned}</span>
            </li>
          {/each}
        </ul>
      </div>
    {/if}
  {/if}
</div>
